/* Desktop PC & Responsive */
.nav {
  overflow:hidden;
  background-color: #4A6C89;
  color: white;
  padding: 20px 10px;
  background-image: -webkit-linear-gradient(top, #4A6C89, #123451);
  background-image: linear-gradient(to bottom, #4A6C89, #123451)
}

.nav a {
  float: left;
  color: white;
  text-align: center;
  padding: 12px;
  text-decoration: none;
  font-size: 18px;
  line-height: 25px;
  border-radius: 4px;
}

.nav a:hover {
  color: #A4CAE9;
}

.nav a.logo {
  font-size: 25px;
  font-weight: bold;
}

.nav-right {
  float: right;
}

.nav-left {
  float: left;
}

  /* Ipad Pro COMPLETE */
@media screen and (max-width: 1024px) {
  .nav a {
    font-size: 28px;
  }
}

